<template>
  <div class="gift">
    <!-- 表单输入框 -->
    <div class="form">
      <div class="form__tips">填写手机号<span>*该手机号用于报名课程使用</span></div>
      <div class="form__item">
        <span class="form__label">手机号</span>
        <input
          type="number"
          class="form__phone"
          ref="focusInput"
          placeholder="请输入手机号"
          v-model.trim="phone"
          maxlength="11" />
      </div>
      <div class="form__item">
        <span class="form__label">验证码</span>
        <input
          type="number"
          class="form__code"
          ref="focusInput"
          placeholder="请输入验证码"
          v-model.trim="code" />
        <div class="form__get-code" @click="getPhoneCode">{{ codeTime ? `${codeTime}s` : "获取验证码" }}</div>
      </div>
    </div>
    <!-- giftbox detail -->
    <div class="detail">
      <p class="detail__title1">12天小白理财特训班</p>
      <p class="detail__title2">支付成功后，关注<span>【微淼精品课】</span>公众号，获取上课信息，领取学习礼盒</p>
      <img src="@/assets/giftBoxPay/detail.png" alt="" />
    </div>
    <!-- price -->
    <div class="price">
      <div class="price__item">
        <span>课程原价</span>
        <span class="price__item__num">¥<i>499.9</i></span>
      </div>
      <div class="price__item">
        <span class="price__item__hot">限时特惠</span>
        <span>¥470.0</span>
      </div>
      <div class="price__item">
        <span>附赠<em>价值 269元超值学习礼盒</em></span>
        <span>1 盒</span>
      </div>
      <div class="price__line"></div>
      <div class="price__all">
        <span>总计：</span>
        <span>¥<i>29.9</i></span>
      </div>
    </div>
    <!-- pay  type -->
    <div class="paytype">
      <p class="paytype__title">支付方式</p>
      <div class="paytype__item">
        <div
          class="paytype__item__list"
          v-for="(item,index) in payTypeData"
          :key="index">
          <div>
            <img :src="item.iconType">
            <p>{{ item.name }}</p>
          </div>
          <img :src="currentType===index?item.iconSel:item.iconNoSel" @click="switchSel(index,item.type)">
        </div>
      </div>
      <!-- 温馨提示 -->
      <div class="paytype__tips">
        <p>温馨提示：</p>
        <p>支付成功后，关注<span>【微淼精品课】</span>公众号，获取上课信息，领取学习礼盒</p>
      </div>
    </div>
    <!-- 倒计时 -->
    <footer>
      <!-- 确认支付 -->
      <div class="pay">
        <div class="pay__left">
          <span class="pay__cell">¥</span>
          <span class="pay___num">29.9</span>
        </div>
        <div class="pay__btn" @click="goPay">确认支付</div>
      </div>
    </footer>
    <!-- 挽留弹框 -->
    <GiftBoxRetain ref="showGiftBoxRetain" @goPay="goPay" />
    <!-- 无库存弹框 -->
    <GiftBoxNoStock ref="showGiftBoxNoStock" />
    <!-- 不能购买 -->
    <GiftBoxNoBuy ref="showGiftBoxNoBuy" />
  </div>
</template>

<script>
import GiftBoxRetain from './modal/GiftBoxRetain'
import GiftBoxNoStock from './modal/GiftBoxNoStock'
import GiftBoxNoBuy from './modal/GiftBoxNoBuy'
import { clickLog } from '@/helper/clickLog'
import { isPhone } from '@/helper/reg'
import { Dialog } from 'vant'
import { mapState } from 'vuex'
import { getSearch } from '@/utils'

export default {
  name: 'GiftBoxPay',
  components: {
    GiftBoxRetain,
    GiftBoxNoStock,
    GiftBoxNoBuy
  },
  filters: {
    // 补0
    addZero (num) {
      if (String(num).split('').length < 2) {
        return `0${num}`
      }
      return num
    }
  },
  data () {
    return {
      phone: '', // 手机号
      code: '', // 验证码
      codeTime: 0, // 验证码 倒计时 时间
      timer: null, // 轮询定时器
      isClick: false,
      payTypeData: [
        {
          iconType: require('@/assets/giftBoxPay/icon_wx.png'),
          name: '微信支付',
          iconSel: require('@/assets/giftBoxPay/icon_sel.png'),
          iconNoSel: require('@/assets/giftBoxPay/icon_nosel.png'),
          type: 2
        },
        {
          iconType: require('@/assets/giftBoxPay/icon_zfb.png'),
          name: '支付宝支付',
          iconSel: require('@/assets/giftBoxPay/icon_sel.png'),
          iconNoSel: require('@/assets/giftBoxPay/icon_nosel.png'),
          type: 1
        }
      ],
      pay_type: 2, // 支付方式  1支付宝  2微信  默认是微信
      currentType: 0,
      timerLoop: false
    }
  },
  computed: {
    ...mapState('giftBox', ['productSn', 'link'])
  },
  created () {
    // 查看是否已经点击过确认支付按钮了  如果已经点击过并且掉接口成功  会存储上一次的手机号+验证码  用作回显
    if (localStorage.getItem('giftBoxPayFormData')) {
      const { data, expire, time } = JSON.parse(localStorage.getItem('giftBoxPayFormData'))
      if ((new Date().getTime() - time) < expire) {
        const { phone, code, pay_type: payType, currentType } = data
        this.phone = phone
        this.code = code
        this.pay_type = payType || 2
        this.currentType = currentType || 0
        /**
         *  检查订单  有些手机支付完成后不会跳转到中间页面 而是返回到这个页面
         *  如果查询到手机号  再开启轮询查
         */
        if (phone && /^[1]([3-9])[0-9]{9}$/.test(data.phone)) {
          this.checkoutOrder()
          this.timerLoop = setInterval(() => {
            this.checkoutOrder()
          }, 3000)
        }
      }
    }
    // 广点通渠道不支持支付宝
    if (getSearch('medId') && Number(getSearch('medId').substring(0, 2)) === 11) {
      this.pay_type = 2
      this.payTypeData.pop()
    }
    // 挽留弹窗
    // 避免浏览器打开新窗口  需要手动弹起挽留弹窗
    if (localStorage.getItem('giftBoxPayRetain')) {
      setTimeout(() => {
        this.$refs.showGiftBoxRetain.open()
      }, 1300)
    }
  },
  mounted () {

  },
  methods: {
    /**
     *检查订单状态
     */
    async checkoutOrder () {
      try {
        await this.$apis.giftBox.checkOrder({
          phone: this.phone,
          product_sn: this.productSn
        })
        // 1000 留在当前页面  继续完成支付  不做任何操作
      } catch (err) {
        clearInterval(this.timerLoop)
        const { code, data: { order_num: orderNum } } = err
        if (code === 30006 || code === 30005) {
          localStorage.setItem('giftBoxOrderNum', orderNum)// 存储order_num  在地址页面会用到
          this.$router.replace('/GiftBoxPaySuccess')
        }
      }
    },
    /**
     * @description 获取验证码
     */
    async getPhoneCode () {
      try {
        const { phone, codeTime } = this
        if (codeTime) return
        if (!isPhone(phone)) {
          return
        }
        // 获取验证码
        await this.$apis.userOperator.getPhoneCode({
          phone,
          product_sn: this.productSn
        })
        this.codeTime = 60
        // 倒计时
        this.timerObj = setInterval(() => {
          this.codeTime--
          if (!this.codeTime)clearInterval(this.timerObj)
        }, 1000)
      } catch (err) {
        const { msg } = err
        Dialog({ message: msg })
      } finally {
        clickLog({
          module_ori: '获取验证码',
          module_part: 'xby_lhtf_code_click'
        })
      }
    },
    /**
     * @description 确认支付
     */
    async goPay () {
      if (this.isClick) {
        return
      }
      this.isClick = true
      const { origin, pathname, search, hash } = location
      try {
        if (!isPhone(this.phone)) return
        if (!this.code) {
          Dialog({ message: '请填写验证码' })
          return
        }

        const data = await this.$apis.giftBox.pay({
          phone: this.phone,
          code: this.code,
          pay_type: this.pay_type,
          ext_param: [],
          return_url: encodeURIComponent(`${origin}${pathname}${search}#/GiftBoxWhite?${hash.split('?')[1]}`), // 确定是否需要携带参数
          product_sn: this.productSn,
          ...getSearch(),
          protocol: window.location.protocol,
          host: window.location.host,
          link: this.link ? this.link
            .replace(window.location.protocol + '//', '')
            .replace(window.location.host, '')
            .split('&wm_click_id')[0]
            : window.location.href
              .replace(window.location.protocol + '//', '')
              .replace(window.location.host, '')
              .split('&wm_click_id')[0]
        })
        localStorage.setItem('giftBoxPhone', this.phone)
        // pay_type  1 支付宝  2微信
        if (this.pay_type === 1) {
          this.aliPay(data)
        } else if (this.pay_type === 2) {
          this.wxPay(data)
        }
        localStorage.setItem('giftBoxPayRetain', true)// 避免打开新窗口  需要手动弹起挽留弹窗
        // 打开挽留弹窗  因为有的手机不刷新 所以需要再这里打开一下
        setTimeout(() => {
          this.$refs.showGiftBoxRetain.open()
        }, 1000)
      } catch (err) {
        const { code, data: { order_num: orderNum } } = err
        if (code === 35001) {
          this.$refs.showGiftBoxNoStock.toggleShow()
        }
        if (code === 30006) { // 已经支付过了 查询是否已经填写了地址
          localStorage.setItem('giftBoxOrderNum', orderNum)// 更新order_num
          this.$router.push('/GiftBoxPaySuccess')
        }
      } finally {
        this.isClick = false
        const obj = {
          data: {
            phone: this.phone,
            code: this.code,
            pay_type: this.pay_type,
            currentType: this.currentType
          },
          time: new Date().getTime(),
          expire: 60 * 1000 * 15
        }
        // localStorage 设置的值不能为对象,转为json字符串
        localStorage.setItem('giftBoxPayFormData', JSON.stringify(obj))// 我已经点击过确认支付标识
        clickLog({
          module_ori: '确认支付',
          module_part: 'xby_lhtf_buy_click'
        })
      }
    },
    // 微信支付
    wxPay ({ url }) {
      window.location.href = decodeURIComponent(url)
    },
    // 支付宝支付
    aliPay (data) {
      // this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.push({
        ...this.$route,
        query: {
          ...query
        }
      })
    },
    /**
     * @description 切换支付方式
     */
    switchSel (index, type) {
      this.currentType = index
      this.pay_type = type
    }
  }
}
</script>

