<template>
  <BaseModal :show="show" :is-scroll="true">
    <div class="retain">
      <div class="retain__btn" @click="rePay"></div>
      <div class="retain__giveup" @click="close">放弃优惠</div>
    </div>
  </BaseModal>
</template>

<script>
import BaseModal from '@/components/BaseModal'
import { clickLog } from '@/helper/clickLog'
export default {
  name: 'GiftBoxRetain',
  components: {
    BaseModal
  },
  data () {
    return {
      show: false
    }
  },
  created () {
  },
  methods: {
    /**
     *  @description:打开
     */
    open () {
      this.show = true
    },
    /**
     *  @description:关闭
     */
    close () {
      this.show = false
      localStorage.removeItem('giftBoxPayRetain')// 清除挽留弹窗 需要弹出的状态
      clickLog({
        module_ori: '放弃优惠',
        module_part: 'xby_lhtf_pop_cancel_click'
      })
    },
    /**
     *  @description:立即支付
     */
    rePay () {
      this.$emit('goPay')
      this.close()
      clickLog({
        module_ori: '立即支付',
        module_part: 'xby_lhtf_pop_buy_click'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.retain {
  @include bgImg("~@/assets/giftBoxPay/bg_modal.png",750px,706px);
  @include verCenter();

  flex-direction: column;
  margin-top: -60px;

  // 支付按钮
  .retain__btn {
    @include bgImg("~@/assets/giftBoxPay/btn_modal.png",454px,130px);

    margin-top: 456px;
  }
  // 放弃优惠
  .retain__giveup {
    font-size: 26px;
    color: #fae6c9;
    margin-top: 10px;
    opacity: 0.6;
  }
}
</style>
